import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { InputGroupInline, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../../dependencies/empresaMLFDependency';
import { getDefaultPrinterDataBasic, PrinterDataBasic } from '../../../../../models/dtos/ger/printerDataBasic';
import { MONTHYEAR } from '../../../../../models/enumerated/ger/printerConfigTypeEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportFormView from '../../../report/ReportFormView';
import { CLASS_KEY, REPORT_EVENT, REPORT_TITLE, ResumoImpostoRV } from './ResumoImpostoRV';

const BASIC_DATA: PrinterDataBasic = {
  ...getDefaultPrinterDataBasic(REPORT_TITLE, REPORT_EVENT, CLASS_KEY, MONTHYEAR.value),
  refInicial: '201712',
};

export default function ResumoImpostoReportView(props: WindowComponentProps) {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const history = useHistory();
  const formProps = useForm<ResumoImpostoRV>({ mode: 'onBlur' });

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa !== null) {
        setTimeout(() => {
          formProps.setValue('idGerEmpresa', globalReducer.globalParameter.empresa.id);
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent
      {...props}
      title={REPORT_TITLE}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
    >
      <ReportFormView formProps={formProps} printerData={BASIC_DATA} history={history} extraConfigOpened={true}>
        <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />
      </ReportFormView>
    </WindowComponent>
  );
}
