import { GlobalParameter } from '../models/dtos/ger/globalParameter';

export function hasEmpresaMLF(globalParameter: GlobalParameter): string | undefined {
  let result = null;
  if (!globalParameter.empresaMLF) {
    result =
      'A empresa fiscal da empresa selecionada deve estar configurada. Para configurar acesse:\n' +
      'Módulo Fiscal > Manutenção > Configurações > Empresa';
  }
  return result;
}
