import { SIMPLE } from '../../enumerated/ger/printerConfigTypeEnum';
import { GRAPHIC } from '../../enumerated/ger/printerTypeEnum';
import { PrinterDataEmail } from './printerDataEmail';

export interface PrinterDataBasic {
  printerTypeEnum?: string;
  dtInicial?: string;
  dtFinal?: string;
  refInicial?: string;
  refFinal?: string;
  reportSequence?: boolean;
  pageNumber?: string;
  title?: string;
  printerDataEmail?: PrinterDataEmail;
  passObj?: any;
  printerConfigTypeEnum?: string;

  dtInicialLabel?: string;
  dtFinalLabel?: string;
  refInicialLabel?: string;
  refFinalLabel?: string;
  anoLabel?: string;

  reportEventType: string;
  classKey: string;
  module: string;
}

export const CLASS_NAME = 'br.com.projetusinformatica.calima.ger.model.dto.printerdatabasicdto';

export function getDefaultPrinterDataBasic(
  title: string,
  reportEventType: string,
  classKey: string,
  printerConfigTypeEnum: string = SIMPLE.value,
  module: string = 'MLF'
) {
  return {
    anoLabel: 'Ano',
    classKey,
    dtFinalLabel: 'Data Final',
    dtInicialLabel: 'Data Inicial',
    module,
    pageNumber: '1',
    passObj: {},
    printerConfigTypeEnum,
    printerDataEmail: {},
    printerTypeEnum: GRAPHIC.value,
    refFinalLabel: 'Referência Final',
    refInicialLabel: 'Referência Inicial',
    reportEventType,
    reportSequence: false,
    title,
  };
}
