import { saveAs } from 'file-saver';
import React, { FC, useEffect } from 'react';
import { Col, CustomInput, Form, Row } from 'reactstrap';
import {
  Accordion,
  AccordionItem,
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  currentMomentDate,
  ErroList,
  ERROR,
  format,
  formatDate,
  INFORMATION,
  InputGroupInline,
  InputType,
  ProcessBar,
  REF_PATTERN,
  showToast,
  UseFormProps,
} from 'summer';
import { PrinterDataBasic } from '../../../models/dtos/ger/printerDataBasic';
import { DATE, DATES, MONTHYEAR, MONTHYEARS, YEAR } from '../../../models/enumerated/ger/printerConfigTypeEnum';
import PRINTER_TYPE_ENUM, { GRAPHIC } from '../../../models/enumerated/ger/printerTypeEnum';
import { downloadOldReport, gerarOldReport } from '../../../services/report.service';

export interface ReportWindowComponentProps {
  formProps: UseFormProps;
  printerData?: PrinterDataBasic;
  history?: any;
  /** Função de validações extras */
  validateExtras?: (printerData: PrinterDataBasic, formData: any) => string[];
  /** já iniciar as opções extras do relatório expandidas? */
  extraConfigOpened?: boolean;
}

const ReportFormView: FC<ReportWindowComponentProps> = props => {
  useEffect(
    () => {
      const { printerTypeEnum, printerConfigTypeEnum } = props.printerData;
      let resetObject: any = {
        ...props.printerData,
        printerTypeEnum: printerTypeEnum && printerTypeEnum.length > 0 ? printerTypeEnum[0] : GRAPHIC.value,
      };

      const current = currentMomentDate();
      const endDate = currentMomentDate();
      switch (printerConfigTypeEnum) {
        case DATE.value:
          resetObject = {
            ...resetObject,
            dtInicial: formatDate(current.toDate()),
          };
          break;
        case DATES.value:
          resetObject = {
            ...resetObject,
            dtFinal: formatDate(endDate.endOf('month').toDate()),
            dtInicial: formatDate(current.toDate()),
          };
          break;
        case MONTHYEAR.value:
          resetObject = {
            ...resetObject,
            referenciaInicial: format(current.toDate(), REF_PATTERN),
          };
          break;
        case MONTHYEARS.value:
          resetObject = {
            ...resetObject,
            referenciaFinal: format(current.toDate(), REF_PATTERN),
            referenciaInicial: format(endDate.toDate(), REF_PATTERN),
          };
          break;
        case YEAR.value:
          resetObject = {
            ...resetObject,
            ano: String(current.year()),
          };
          break;
        default:
          break;
      }

      props.formProps.reset(resetObject);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [props.printerData.printerConfigTypeEnum, props.printerData.printerTypeEnum]
  );

  const onSubmit = (data: any, event: any) => {
    event.preventDefault();
    let erros = [];
    if (props.validateExtras) {
      erros = props.validateExtras(props.printerData, data);
    }
    if (erros && erros.length) {
      const params: AlertProps = {
        message: <ErroList erros={erros} />,
        type: AlertType.ERROR,
      };
      alertManager.emit(ACTION_EVENTS.SHOW, params);
    } else {
      props.printerData.passObj = data;
      showToast('Seu relatório está sendo gerado...', { ...INFORMATION, delay: 3000 });
      gerarOldReport(props.printerData, {
        errorFunction: (mensagem: string) => {
          showToast(mensagem, ERROR);
        },
        thenFunction: () => {
          downloadOldReport({
            errorFunction: (mensagem: string) => {
              showToast(mensagem, ERROR);
            },
            thenFunction: (response: any) => {
              const contentDisposition = response.headers['content-disposition'];
              const filename = contentDisposition
                .slice(0, contentDisposition.lastIndexOf(';'))
                .split('filename=')[1]
                .replace(/"/g, '');
              saveAs(response.data, filename);
            },
          });
        },
      });
    }
  };

  const restoreTitle = () => {
    props.formProps.setValue('title', props.printerData.title, true);
  };

  const dateInitial = (
    <InputGroupInline
      id="dtInicial"
      label={props.printerData.dtInicialLabel}
      isPrependLabel={true}
      formProps={{ ...props.formProps, validation: { required: true } }}
      type={InputType.DATE}
    />
  );

  const dateComponent = props.printerData.printerConfigTypeEnum === DATE.value ? dateInitial : null;

  const datesComponent =
    props.printerData.printerConfigTypeEnum === DATES.value ? (
      <>
        {dateInitial}

        <InputGroupInline
          id="dtFinal"
          label={props.printerData.dtFinalLabel}
          isPrependLabel={true}
          formProps={{ ...props.formProps, validation: { required: true } }}
          type={InputType.DATE}
        />
      </>
    ) : null;

  const referenceInitial = (
    <InputGroupInline
      id="refInicial"
      label={props.printerData.refInicialLabel}
      isPrependLabel={true}
      formProps={{ ...props.formProps, validation: { required: true } }}
      type={InputType.REFERENCE}
    />
  );

  const referenceComponent = props.printerData.printerConfigTypeEnum === MONTHYEAR.value ? referenceInitial : null;

  const referencesComponent =
    props.printerData.printerConfigTypeEnum === MONTHYEARS.value ? (
      <>
        {referenceInitial}

        <InputGroupInline
          id="refFinal"
          label={props.printerData.refFinalLabel}
          isPrependLabel={true}
          formProps={{ ...props.formProps, validation: { required: true } }}
          type={InputType.REFERENCE}
        />
      </>
    ) : null;

  const anoComponent =
    props.printerData.printerConfigTypeEnum === YEAR.value ? (
      <InputGroupInline
        id="ano"
        label={props.printerData.anoLabel}
        isPrependLabel={true}
        formProps={{ ...props.formProps, validation: { required: true } }}
        inputMaskedProps={{ mask: '9999' }}
        type={InputType.MASK}
      />
    ) : null;

  return (
    <Form noValidate={true}>
      {props.children}

      <Accordion formProps={props.formProps} firstOpen={props.extraConfigOpened || false}>
        <AccordionItem title="Configurações adicionais do Relatório">
          <InputGroupInline
            id="title"
            label="Título"
            isPrependLabel={true}
            placeholder="Título"
            iconAppend="undo-alt"
            appendClickHandler={restoreTitle}
            formProps={{
              ...props.formProps,
              validation: {
                maxLength: 120,
                minLength: 5,
                required: true,
              },
            }}
          />

          <InputGroupInline
            id="pageNumber"
            label="Página Inicial"
            isPrependLabel={true}
            placeholder="Página Inicial"
            formProps={{
              ...props.formProps,
              validation: {
                min: 0,
                required: true,
              },
            }}
            type={InputType.NUMBER}
          />

          <Row className="mb-3 mt-3">
            <Col>
              <CustomInput
                type="switch"
                label="Sequenciar Relatórios"
                id="reportSequence"
                name="reportSequence"
                innerRef={props.formProps.register}
              />
            </Col>
          </Row>

          {dateComponent}
          {datesComponent}
          {referenceComponent}
          {referencesComponent}
          {anoComponent}

          <InputGroupInline
            id="printerTypeEnum"
            label="Tipo de Impressão"
            isPrependLabel={true}
            placeholder="Selecione o tipo de impressão"
            formProps={{ ...props.formProps, validation: { required: true } }}
            inputDropDownProps={{
              optionsProvider: PRINTER_TYPE_ENUM,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </AccordionItem>
      </Accordion>

      <ProcessBar handleSubmit={props.formProps.handleSubmit(onSubmit)} hideCancelButton={true} />
    </Form>
  );
};

export default ReportFormView;
